@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.citas {
    list-style: none;
    padding: 0;

    li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;

        p {
            font-weight: v.$bold;
            color: v.$azul;
        }

        span, .servicio {
            font-weight: v.$regular;
            color: v.$blanco;
        }

        &:hover {
            background-color: color.adjust(v.$negro, $lightness: 10%);
        }
    }
}

.servicios {
    @extend .citas;

    .acciones {
        margin: 0;
    }

    .boton, .boton-eleiminar {
        margin: 0;
    }

}